.image-1 {
	width: 100%;
	object-fit: contain;
}

.small-photos {
	width: 325px;
	height: 400px;
}

.other-images {
	width: 100%;
	object-fit: contain;
	height: 192px;
}

.image-container {
	height: 400px;
}

.sub-image {
	height: 192px;
}

.gallery-img {
	filter: brightness(0.65);
}

.centered {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.bottom-left {
	bottom: 5%;
	left: 5%;
	transform: none;
}

.border-verified {
	border: 1px solid var(--success-3);
}

.icon-14px {
	font-size: 14px;
	width: 14px;
	height: 14px;
}

.h-25px {
	height: 25px;
}
